<template>
  <section class="auth">
    <img class="auth_bg" src="@/assets/course/auth.png" alt="djoon">
    <div class="auth_wrap">
        <div class="auth_body">
            <router-link to="/" class="header__logo">
                <img
                class="auth_logo"
                src="@/assets/images/djoon_logo.svg"
                width="173"
                height="48"
                alt="Logo"
                />
            </router-link>
            <div class="auth_tabs">
                <div class="auth_tabs_tab" :class="{'active': signinPage}" @click="signinPage = true">
                    Войти
                </div>
                <div class="auth_tabs_tab" :class="{'active': !signinPage}" @click="signinPage = false">
                    Регистрация
                </div>
            </div>
            <div class="auth_inputs">
                <template v-if="signinPage">
                    <BaseInput 
                        v-model="account.iin" 
                        placeholder="ИИН" 
                        class="input"
                        :onerror="($v.account.iin.$dirty && $v.account.iin.$invalid) ? $t('IIN_required') : ''"
                    />
                    <BaseInput 
                        :type="showPass ? 'text' : 'password'" 
                        :isPassword="true" 
                        @togglePassword="showPass = !showPass"
                        v-model="account.password" 
                        placeholder="Пароль" 
                        class="input" 
                        :onerror="($v.account.password.$dirty && $v.account.password.$invalid) ? $t('Password_required') : ''"
                    />
                    <BaseButton @click="signin" width="100%">
                        Войти
                    </BaseButton>
                </template>
                <template v-else>
                    Регестрация
                </template>
                <div class="auth_link">
                    Забыл пароль
                </div>
            </div>
        </div>
    </div>
  </section>
</template>

<script>
import {integer, maxLength, minLength, required} from "vuelidate/lib/validators";
export default {
    name: 'Auth',
    data() {
        return {
            step: 1,
            email: '',
            block:'login',
            loading: false,
            showModal: false,
            showPass: false,
            signinPage: true,
            account: {
                iin: '',
                password: ''
            }
        }
    },
    methods: {
        signup() {
        if (this.step == 1) {
            this.account.password = this.account.iin
            this.$http.post(API_ROOT + `/api/auth/login`, this.account)
            .then((res) => {
                this.step = 2
                localStorage.setItem("token", res.body.token);
            })
            .catch((err) => {
                Vue.toastr({
                message: 'Error',
                description: err.body.message,
                type: 'error'
                })
            })
        }
        if (this.step == 2) {
            this.$http.post(API_ROOT + `/api/user/email/set`, {'email': this.email, 'token': this.emailToken})
            .then((res) => {
                localStorage.removeItem("token")
                localStorage.removeItem("user")
                this.showModal = true
                Vue.toastr({
                message: 'Success',
                description: res.body.message,
                type: 'success'
                })


            })
            .catch((err) => {
                Vue.toastr({
                message: 'Error',
                description: err.body.message,
                type: 'error'
                })
            })
        }
        },
        signin() {
            this.loading = true
            let postData = {
                iin: this.account.iin,
                password: this.account.password,
            }

            this.$v.$touch()
            if (this.$v.account.$invalid) return

            this.$store.dispatch('login', postData)
            

            // this.$http.post(API_ROOT + `/api/auth/login`, postData)
            //     .then((res) => {
            //         localStorage.setItem('token', res.body.token)
            //         localStorage.setItem('user', JSON.stringify(res.body.user))
            //         localStorage.setItem('has_email', res.body.has_email)
            //         if (res.body.has_email == false) {
            //             this.block = 'activate'
            //             this.step = 2
            //         } else {
            //             this.$router.push({name: 'profile'})
            //         }

            //         this.loading = false;
            //         // this.$router.push({ name: 'profile' })
            //     })
            //     .catch((err) => {

            //         if (err.status === 422 && err.body.need_verification === true) {
            //             this.showModal = true
            //         }

            //         this.loading = false;
            //         console.log(err)
            //     })
        },
    },

    validations: {
        account: {
        iin: {
            required,
            minLength: minLength(12),
            maxLength: maxLength(12),
            integer,
        },
        password: {
            required,
            minLength: minLength(6),
        }
        },
    }
}
</script>

